import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { List, ListItem, colors, ListItemText } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { IconButton } from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import MuiLink from '@material-ui/core/Link';
import { globalHistory } from "@reach/router";
import { AuthenticatedTemplate } from "@azure/msal-react";
import { LinkPath } from '../../../../components/Wrappers';

const useStyles = makeStyles(theme => ({
  root: {},
  item: {
    display: 'flex',
    paddingTop: 0,
    paddingBottom: 0
  },
  button: {
    color: colors.blueGrey[800],
    padding: '10px 8px',
    justifyContent: 'flex-start',
    textTransform: 'none',
    letterSpacing: 0,
    width: '100%',
    fontWeight: theme.typography.fontWeightMedium
  },
  icon: {
    color: theme.palette.icon,
    width: 24,
    height: 24,
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(1)
  },
  active: {
    color: theme.palette.primary.main,
    fontWeight: theme.typography.fontWeightMedium,
    '& $icon': {
      color: theme.palette.primary.main
    },
    backgroundColor: '#f5f5f5'
  },
  sideFooter: {
    position: "fixed",
    bottom: theme.spacing(2),
    left: theme.spacing(3)
  },
  link: {
    textDecoration: 'none'
  }
}));

const SidebarNav = props => {
  const classes = useStyles();
  return (
    <div>
      <div>
        <List>
          {props.publicPages.map(function (page, index) {
            return (
              <React.Fragment key={index}>
                <LinkPath to={page.href} className={classes.link}>
                <ListItem button className={(globalHistory.location.pathname === page.href ? classes.active : '')}>
                  <ListItemIcon>{page.icon}</ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItem>
              </LinkPath>
              </React.Fragment>
            )
          })}
        </List>
        <Divider />
        <AuthenticatedTemplate>
        <List>
          {props.privatePages.map(function (page, index) {
            return (
              <React.Fragment key={index}>
                <LinkPath to={page.href} className={classes.link}>
                <ListItem button className={(globalHistory.location.pathname === page.href ? classes.active : '')}>
                  <ListItemIcon>{page.icon}</ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItem>
              </LinkPath>
              </React.Fragment>
            )
          })}
        </List>
        <Divider />
        </AuthenticatedTemplate>        
      </div>
      
      <div className={classes.sideFooter}>
        <Divider />
        <MuiLink href={props.profileLinks.linkedIn} target="_blank">
          <IconButton >
            <LinkedInIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.facebook} target="_blank">
          <IconButton >
            <FacebookIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.twitter} target="_blank">
          <IconButton >
            <TwitterIcon />
          </IconButton>
        </MuiLink>
        <MuiLink href={props.profileLinks.github} target="_blank">
          <IconButton >
            <GitHubIcon />
          </IconButton>
        </MuiLink>
      </div>
    </div>
  );
};

SidebarNav.propTypes = {
  className: PropTypes.string,
  publicPages: PropTypes.array.isRequired,
  privatePages: PropTypes.array.isRequired
};

export default SidebarNav;