import React from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { AppBar } from '@material-ui/core';
import { Toolbar } from '@material-ui/core';
import { Hidden } from '@material-ui/core';
import { IconButton } from '@material-ui/core';
import { Typography } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import SignInSignOutButton from './SignInSignOutButton';
import { LinkPath } from '../../components/Wrappers';

const useStyles = makeStyles(theme => ({
  root: {
    boxShadow: 'none'
  },
  flexGrow: {
    flexGrow: 1
  },
  signOutButton: {
    marginLeft: theme.spacing(1)
  },
  logo: {
    paddingTop: "6px",
    height: "50px"
  },
  pageTitle: {
    marginLeft: '10px',
    [theme.breakpoints.up(780)]: {
      //marginLeft: '150px'
    },
    color: '#fff'
  },
  link: {
    textDecoration: 'none'
  }
}));

const Topbar = props => {
  const { className, onSidebarOpen, ...rest } = props;
  const classes = useStyles();

  return (
    <AppBar
      {...rest}
      className={clsx(classes.root, className)}
    >
      <Toolbar>
        <LinkPath to="/" className={classes.link} >
          <img src="/images/Logo.svg" alt="Bipul Raman" className={classes.logo}></img>
        </LinkPath>
        <LinkPath to="/" className={classes.link}>
          <Typography variant="h5" color="textSecondary" className={classes.pageTitle}>
            Bipul's Web
          </Typography>
        </LinkPath>


        <div className={classes.flexGrow} />
        <Hidden mdDown>
          <SignInSignOutButton></SignInSignOutButton>
        </Hidden>
        <Hidden lgUp>
          <IconButton
            color="inherit"
            onClick={onSidebarOpen}
          >
            <MenuIcon />
          </IconButton>
        </Hidden>
      </Toolbar>
    </AppBar>
  );
};

Topbar.propTypes = {
  className: PropTypes.string,
  onSidebarOpen: PropTypes.func
};

export default Topbar;