import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';
import FavoriteIcon from '@material-ui/icons/Favorite';
import { LinkPath } from '../../components/Wrappers';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  icon: {
    fontSize: 14,
    color: "#ea4335"
  }
}));

const Footer = props => {
  const { className, ...rest } = props;

  const classes = useStyles();

  return (
    <div
      {...rest}
      className={clsx(classes.root, className)}
    >
      <Typography variant="body2" color="textSecondary">
        <LinkPath color="inherit" to="/license" >Copyright  Bipul Raman {new Date().getFullYear()}</LinkPath>{' | '}    
        <LinkPath to="/privacy">Privacy Policy</LinkPath>{' | '}<LinkPath to="/disclaimer">Discliamer</LinkPath>
        {" | Designed & Developed with "}<FavoriteIcon className={classes.icon}  />{" in India  "}<img src="/images/India.svg" alt="India" height="11px"></img>{" by Bipul."}
      </Typography>
    </div>
  );
};

Footer.propTypes = {
  className: PropTypes.string
};

export default Footer;
