import React from 'react';
import { navigate } from 'gatsby';
import { globalHistory } from "@reach/router";
import { Link } from 'gatsby';
import { Helmet } from 'react-helmet';

export function Redirect(routePath) {
    navigate(routePath);
};

export function GetHashParam() {
    var hashParam = undefined;
    if (globalHistory.location.hash !== undefined) {
        hashParam = globalHistory.location.hash.replace("#", '');
        return hashParam;
    }
    else {
        return undefined;
    }
};

export const LinkPath = React.forwardRef((props, ref) => (
    <Link ref={ref} to={props.to} className={props.className} >{props.children}</Link>
));

export const SetMetadata = React.forwardRef((props, ref) => (
    <Helmet ref={ref} >{props.children}</Helmet>
));